.burger2 {
  position: fixed;
  top: 0;
  right: 0;
  width: 48px;
  height: 48px;
  display: none; }
  @media screen and (max-width: 960px) {
    .burger2 {
      display: flex;
      align-items: center;
      left: 50%;
      transform: translateX(-50%);
      margin-top: 7px; } }
  .burger2__span {
    display: block;
    position: relative;
    width: 100%;
    height: 4px;
    background-color: #ff2323;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px; }
    .burger2__span::after {
      content: "";
      position: absolute;
      left: 0;
      top: 12px;
      width: 100%;
      height: 4px;
      background-color: #ff2323;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }
    .burger2__span::before {
      content: "";
      position: absolute;
      left: 0;
      top: -12px;
      width: 100%;
      height: 4px;
      background-color: #ff2323;
      transition: all 0.3s;
      backface-visibility: hidden;
      border-radius: 2px; }

