// FONTS
@import "base/fonts";

//VARIABLES
@import "base/variables";

//MIXINS
@import "base/mixins";

//RESET
@import "base/reset";

//STANDART SETUP
@import "base/standart";

//MEDIA QUERIES
@import "base/media";


@include reset-2();

// .wrapper
//     +wrapper()

// .debug
//     +debug(rgba(192,192,192, .5), 1px solid blue)

// HEADER
.top {
    width: 100%;
    height: 7.8rem;
    background-color: #181818;
    position: relative;

    &_bg-color {
        background-color: #ffffff; } }

.header {
    @include sm() {
        margin: 0 auto;
        padding: 0 1.5rem; }
    background-color: #181818;
    display: flex;
    align-items: center;
    max-width: 117rem;
    margin: 0 5.5rem;
    justify-content: space-between;
    height: 7.8rem;

    &_bg-color {
        background-color: #ffffff; } }

.logo {
    width: 7.8rem;
    height: 2.7rem;
    flex-shrink: 0;

    & img {
        width: 7.8rem;
        height: 2.7rem; } }

.menu {
    @include md() {
        display: none; }
    max-width: 94rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    font-family: Circe, sans-serif;
    font-weight: 700;
    font-size: 14px;
    letter-spacing: 0.47px;

    &__ul {
        display: flex;
        justify-content: flex-end; }

    &__li:last-child {
        @include md() {
            margin-left: 0; }
        margin-left: 25.8rem;
        text-transform: uppercase; }

    &__li:first-child {
        margin-right: 5rem; }

    &__link {
        text-decoration: none;
        color: #ffffff;
        transition: color .3s ease-out;

        &_color {
            color: #000000; }

        &:hover {
            color: $accent; } } }

// // END HEADER

// SIDEBAR
.side-block {
    @include xs() {
        display: none; }
    position: fixed;
    top: 0;
    right: 0;
    width: 5.5rem;
    background-color: #181818;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__social {
        transform: rotate(90deg);
        text-decoration: none;
        font-size: 14px;
        font-family: Circe, sans-serif;
        color: #fff;
        letter-spacing: 1.05px;
        line-height: 4.8rem;
        transition: color .2s ease-out;

        &:hover {
            color: $accent; } } }
// END SIDEBAR

// MAIN
.main {
    @include sm() {
        width: 100%;
        align-items: center;
        justify-content: space-around;
        flex-direction: column;
        padding: 0 1.5rem; }
    margin-top: -1px;
    width: calc(100% - 5.4rem);
    height: calc(100vh - 7.8rem + 1px);
    display: flex;
    justify-content: space-between;
    padding-right: (23rem - 5.5rem);
    padding-left: 23rem;
    position: relative;

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        background-image: url(../img/background/bg.jpg);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        background-color: rgba(0,0,0,1);
        width: 100%;
        height: 100%;
        z-index: -10;
        opacity: .9;
        filter: brightness(40%); } }

.slogan {
    @include sm() {
        padding-top: 0; }

    padding-top: 19.4rem;
    color: #fff;

    &__desc {
        font-size: 1.6rem;
        line-height: 1.4;
        font-family: Circe, sans-serif;
        margin-bottom: 1.6rem; }

    &__title {
        @include xxs() {
            font-size: 2.8rem; }
        font-size: 3.2rem;
        line-height: 1.25;
        font-family: Circe;
        font-weight: 800;
        letter-spacing: 2.4px;
        text-transform: uppercase;
        position: relative; }

    &__icon {
        @include xs() {
            transform: translateX(-25%); }
        @include xxs() {
            display: none; }
        position: absolute;
        top: 0;
        left: 0;
        transform: translateX(-51%);
        z-index: -1;

        & img {
            width: 13rem;
            height: 4.9rem; } } }

.right-side {
    @include sm() {
        padding-top: 0; }
    display: flex;
    padding-top: 21.5rem;
    flex-direction: column;
    align-items: center; }

.count {
    display: flex;
    margin-bottom: 8rem;
    color: #fff;

    &__current {
        font-size: 3.2rem;
        font-weight: 700;
        line-height: .9;
        margin-right: 2.3rem; }

    &__next {
        font-size: 1.1rem;
        line-height: 1.4;
        font-weight: 700;
        position: relative; } }

.next {
    display: flex;
    align-items: center;

    &__line {
        margin-right: 2.3rem;
        display: flex;
        align-items: center; }

    &__right-arrow {
        display: flex;
        align-items: center; } }

// END MAIN

.select-lang {
    @include sm() {
        margin-right: 30px; }
    @include xs() {
        margin-right: 0; }
    border: none;
    background-color: #181818;
    color: #fff;
    cursor: pointer;
    font-size: 14px;
    font-weight: 700;
    font-family: Circe, sans-serif;
    text-transform: uppercase;

    &_color {
        color: #000000;
        background-color: #ffffff; }

    &:focus {
        outline: none; } }

// BURGER MENU
.burger {
    @include lg() {
        margin-right: 2rem; }
    font-size: 14px;
    text-decoration: none;
    text-transform: uppercase;
    color: #ffffff;
    display: block;
    // width: 100px
    display: flex;
    align-items: center;
    transition: color .3s ease-out;

    &_color {
        color: #000000; }

    &:hover {
        color: $accent; }

    &__wrapper {
        margin-right: 1.2rem;
        display: flex;
        flex-direction: column;
        align-items: center; }

    &__span1 {
        width: 2.9rem;
        height: .2rem;
        background: #fff;
        display: block;
        margin-bottom: .5rem;
        position: relative;
        top: -1px;

        &_bg-color {
            background: #000000; } }

    &__span2 {
        width: 2.9rem;
        height: .2rem;
        background: #fff;
        display: block;
        position: relative;
        top: -1px;

        &_bg-color {
            background: #000000; } } }


// burger -2
.burger2 {
    @include md() {
        display: flex;
        align-items: center;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 7px; }
    position: fixed;
    top: 0;
    right: 0;
    width: 48px;
    height: 48px;
    display: none;


    &__span {
        display: block;
        position: relative;
        width: 100%;
        height: 4px;
        background-color: $accent;
        transition: all 0.3s;
        backface-visibility: hidden;
        border-radius: 2px;

        &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 12px;
            width: 100%;
            height: 4px;
            background-color: $accent;
            transition: all 0.3s;
            backface-visibility: hidden;
            border-radius: 2px; }

        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: -12px;
            width: 100%;
            height: 4px;
            background-color: $accent;
            transition: all 0.3s;
            backface-visibility: hidden;
            border-radius: 2px; } } }
// end burger 2


#body {
    display: none; }

// PROJECT PAGE
.project-page {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background-color: #ffffff;
    visibility: hidden; }

.project-single {
    padding: 0 1.5rem; }

.project {
    @include s1440() {
        padding-top: 5rem; }
    @include sm() {
        flex-wrap: wrap;
        padding-top: 3rem; }
    @include xxs() {
        padding-top: 1.4rem; }
    display: flex;
    justify-content: space-between;
    max-width: 82rem;
    margin: 0 auto;
    padding-top: 6rem;

    &__title {
        @include sm() {
            width: 100%; }
        font-size: 36px;
        position: relative;
        text-transform: uppercase;
        font-weight: 700;
        display: block;

        &::before {
            @include full-hd() {
                right: -75%; }
            @include sm() {
                display: none; }
            content: "";
            position: absolute;
            top: 50%;
            right: -55%;
            height: 3px;
            width: 6.6rem;
            transform: translateY(-100%);
            background-color: #000000; } }

    &__description {
        @include sm() {
            text-align: left; }
        font-size: 19px;
        line-height: 1.2;
        text-align: right; } }



.project__count {
    position: absolute;
    right: 0;
    bottom: 0;
    color: #cccccc;
    font-size: 6.4rem;
    line-height: 1; }

.project-foto {
    @include s1440() {
        padding-top: 8rem; }
    @include sm() {
        flex-wrap: wrap;
        padding-top: 5rem; }
    @include xxs() {
        padding-top: 3.6rem; }

    padding-top: 10rem;
    display: flex;
    max-width: 82rem;
    margin: 0 auto;
    align-items: center;
    position: relative;

    &__wrapper-img {
        @include sm() {
            margin-bottom: 2rem; }

        width: 45rem;
        height: 30rem;
        margin-right: 8rem; }

    &__img {
        width: 45rem;
        height: 30rem; }

    &__wrapper-title {
        width: 30rem;
        position: relative; }

    &__title {
        font-size: 20px;
        font-weight: 700;
        text-decoration: underline;
        text-decoration-color: #868686;
        margin-bottom: 1rem;

        & q {
            quotes: "“" "”" "‘" "’"; }

        &::before {
            @include sm() {
                display: none; }
            content: "2018";
            color: #868686;
            font-size: 14px;
            font-weight: 700;
            position: absolute;
            top: -50%;
            left: 0; } }

    &__text {
        color: #868686; } }

.bottom-block {
    display: flex;
    padding-left: 6rem;
    padding-right: 23rem;
    align-items: center;
    width: 100%;
    height: 9rem;
    background-color: #ffffff;
    transform: translateX(100%);
    position: fixed;
    bottom: 0;
    left: 0;

    &__projects {
        font-size: 14px;
        color: #070707;
        font-weight: 700;
        letter-spacing: 0.47px;
        text-transform: uppercase;
        text-decoration: none;
        flex-basis: 18rem;
        opacity: 0; }

    &__done {
        color: #868686;
        font-size: 14px;
        flex-basis: 23.8rem;
        opacity: 0; }

    &__develop {
        color: #868686;
        font-size: 14px;
        flex-basis: 23.8rem;
        opacity: 0; }

    &__icon {
        width: 3.3rem;
        height: 3.3rem;
        opacity: 0;

        & img {
            width: 3.3rem;
            height: 3.3rem; } } }

// END PROJECT PAGE



